* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Fira Sans", sans-serif;
}

.header,
footer {
  text-align: center;
  font-size: 20px;
  padding: 8px;
  width: 100%;
  background-color: rgb(49, 46, 46);
  color: white;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.footer {
  position: absolute;
  top: 770px;
}

/* .background-image {
  width: 210%;
  height: 70vh;
} */

.container {
  font-family: "Fira Sans", sans-serif;
  font-size: 20px;
  text-align: center;
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* position: absolute;
  top: 680px;
  left: 40px; */
  color: black;
}

.add-shop {
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 10px;
}

.add {
  margin-right: 12px;
  color: black;
}

.shop-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}

.hide-shop-container {
  visibility: hidden;
  height: 0;
}

.show-shop-container {
  visibility: visible;
}

.shop-image,
.selected-shop-image {
  width: 40%;
  max-height: 70px;
  padding: 5px;
  margin: 8px;
}

.selected-shop-image {
  display: block;
  margin: 0 auto;
  margin-bottom: 8px;
}

.input-container {
  display: flex;
  justify-content: center;
  margin-bottom: 8px;
  border-radius: 5px;
}

.list-input {
  width: 75%;
  padding: 5px;
  font-size: 20px;
  text-align: center;
  border: black solid 0.5px;
}

.add-btn {
  padding: 5px;
  font-size: 20px;
  text-align: center;
  background-color: rgb(29, 233, 29);
  font-weight: 800;
  border: black solid 0.5px;
}

.list-container {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: black solid 1px;
  background-color: rgb(179, 174, 174);
  max-width: 88%;
  margin-bottom: 10px;
  padding: 5px;
  border-radius: 5px;
}

